.big-container {
  margin: 0px;
  padding: 0px;
  height: 100vh;
  width: 100%;
  background-color: #011627;
  background-image: url(./images/outside-duotone.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
}

.title {
  color: #fffffc;
  text-align: center;
  padding-top: 120px;
  font-family: 'Lobster', cursive;
}

.big-title {
  color: #6ca6c1;
  font-size: 90px;
  text-shadow: 2px 2px #000;
}

.small-title {
  padding-top: 80px;
  margin-bottom: 30px;
  font-size: 45px;
  font-family: 'Varela Round', sans-serif;
  text-shadow: 2px 2px #6ca6c1;
}

.middle {
  width: 100%;
  height: 380px;
  background-image: url(./images/inside.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  filter: grayscale(60%);
  background-size: cover;
  border-top: 2px solid #6ca6c1;
  border-bottom: 2px solid #6ca6c1;
}

.icons {
  position: absolute;
  width: 100%;
  bottom: 40px;
  text-align: center;
}

.icon-left,
.icon-right {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  color: transparent;
  cursor: pointer;
  display: inline-block;
  line-height: 150px;
  font-size: 25px;
  font-family: 'Lobster', cursive;
  user-select: none;
  text-decoration: none;
}

.icon-left:active,
.icon-right:active {
  font-size: 30px;
}

.icon-left {
  background-image: url(./images/silverware-1667988_640.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-right: 5px;
}

.icon-right {
  border-radius: 50%;
  background-image: url(./images/location.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-left: 5px;
}

.icon-left:hover,
.icon-right:hover {
  background-image: none;
  background-color: #6ca6c1;
  color: #222;
}

/*MENU*/

.menu-background {
  height: 90vh;
  width: 600px;
  margin: auto;
  background-color: #011627;
  background-image: url(./images/menu.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 2px solid #6ca6c1;
}

.menu {
  display: flex;
  justify-content: space-between;
  padding-top: 220px;
  flex-wrap: wrap;
  font-family: 'Varela Round', sans-serif;
}

.menu-item,
.menu-price {
  color: #fffffc;
  font-size: 25px;
  flex-basis: 50%;
  padding-top: 20px;
}

.menu-item {
  display: inline-block;
  position: relative;
  margin-left: 0px;
  padding-left: 0px;
}

.icon-left-menu,
.icon-right-location {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  cursor: pointer;
  display: inline-block;
  line-height: 150px;
  font-size: 25px;
  font-family: 'Lobster', cursive;
  user-select: none;
  text-decoration: none;
  background-color: #8fc0a9;
  color: #222;
}

.icon-left-menu {
  margin-right: 5px;
}

.icon-right-location {
  margin-left: 5px;
}

/*LOCATION*/

.address,
.hours {
  padding-top: 80px;
  margin-bottom: 30px;
  font-size: 45px;
  font-family: 'Varela Round', sans-serif;
  text-shadow: 2px 2px #000;
  color: #fffffc;
}

.map {
  height: 400px;
  width: 600px;
  margin: auto;
  background-color: #011627;
  background-image: url(./images/map.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 2px solid #6ca6c1;
}

@media all and (max-height: 870px) {
  .title {
    padding-top: 20px;
  }
  .middle {
    height: 280px;
  }

  .menu {
    padding-top: 70px;
  }

  .address,
  .hours {
    padding-top: 20px;
    margin-bottom: 10px;
    font-size: 35px;
    font-family: 'Varela Round', sans-serif;
    text-shadow: 2px 2px #000;
    color: #fffffc;
  }

  .map {
    height: 300px;
    width: 500px;
    margin: auto;
  }

  .icon-left,
  .icon-right,
  .icon-left-menu,
  .icon-right-location {
    height: 100px;
    width: 100px;
    line-height: 100px;
  }

  .icons {
    bottom: 10px;
    text-align: center;
  }
}

@media all and (max-width: 768px) {
  .title {
    padding-top: 50px;
  }

  .big-title {
    font-size: 60px;
  }

  .small-title,
  .address {
    padding-top: 50px;
    margin-bottom: 50px;
    font-size: 30px;
  }

  .middle {
    height: 280px;
  }

  .icon-left,
  .icon-right,
  .icon-left-menu,
  .icon-right-location {
    height: 100px;
    width: 100px;
    line-height: 100px;
  }

  .menu-background,
  .map {
    width: 100%;
  }

  .menu {
    padding-top: 150px;
  }

  .hours,
  .address {
    font-size: 20px;
    padding-top: 0px;
  }
}
